import React, { useEffect } from "react";
import {
  Grid,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  TextField,
  Paper,
  Typography,
  makeStyles,
  ListItemIcon
} from "@material-ui/core";
import CancelIcon from "@material-ui/icons/Cancel";
import CheckIcon from "@material-ui/icons/Check";
import CodeSnippet from "./CodeSnippet";
import {
  useValidator,
  requiredValidation,
  lengthValidation,
  asyncValidation
} from "./hook";
import "./App.css";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: "#f5f5f5",
    padding: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.primary
  },
  paperCenter: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.primary
  },
  inline: {
    display: "block"
  },
  addTripButton: { position: "fixed", right: "15px", bottom: "15px" }
}));

export default () => {
  const classes = useStyles();
  const asyncFunction = async (value, context) => {
    const response = await fetch("https://jsonplaceholder.typicode.com/photos");
    const res = await response.json();
    if (value != res.length) {
      throw new Error(
        "Custom Async error when value is not equal to " +
          res.length +
          " which is the response array length!"
      );
    }
  };
  const validators = [
    requiredValidation(),
    lengthValidation(2, 5),
    asyncValidation(asyncFunction)
  ];
  const { error, onChange, pending } = useValidator(validators);

  const changeHandler = e => {};

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Paper className={classes.paperCenter}>
          <Typography gutterBottom variant="h2" color="textPrimary">
            React Input Validation
          </Typography>
          <Typography variant="h5" color="textPrimary">
            Implement Sync and Async validations using simple hooks! 🎉🎉
          </Typography>
        </Paper>
      </Grid>
      <Grid item xs={12} md={6}>
        <Paper className={classes.paper}>
          <Typography variant="h4" color="textPrimary">
            Features
          </Typography>
          <List>
            <ListItem>
              <ListItemIcon>
                <CheckIcon />
              </ListItemIcon>
              <ListItemText primary="Easy to use hook API, Specify a list of validations that should run"></ListItemText>
            </ListItem>
            <ListItem>
              <ListItemIcon>
                <CheckIcon />
              </ListItemIcon>
              <ListItemText
                primary="Supports Asynchronous validations as well."
                secondary={
                  <Typography
                    component="span"
                    variant="body2"
                    color="textSecondary"
                    className={classes.inline}
                  >
                    you can have a combination of both sync and async validators
                  </Typography>
                }
              ></ListItemText>
            </ListItem>

            <ListItem>
              <ListItemIcon>
                <CheckIcon />
              </ListItemIcon>
              <ListItemText
                primary="Returns a list of formatted error messages for user feedback"
                secondary={
                  <Typography
                    component="span"
                    variant="body2"
                    color="textSecondary"
                    className={classes.inline}
                  >
                    Specify fetch size, limit and scroller to monitor fetch
                    behaviour.
                  </Typography>
                }
              ></ListItemText>
            </ListItem>
            <ListItem>
              <ListItemIcon>
                <CheckIcon />
              </ListItemIcon>
              <ListItemText primary="Out of the box support for required, length, range, regular expression and custom async validations"></ListItemText>
            </ListItem>
          </List>
        </Paper>
      </Grid>
      <Grid item xs={12} md={6}>
        <Paper className={classes.paper}>
          <CodeSnippet
            title="Install"
            text="npm install react-validation-hook"
          />
          <CodeSnippet
            title="Usage"
            text={`
                  import {
                    useValidator,
                    requiredValidation,
                    lengthValidation,
                    asyncValidation
                  } from "react-validation-hook";`}
          />
        </Paper>
      </Grid>
      <Grid item container xs={12} md={12}>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            <Typography variant="h4" color="textPrimary">
              Demo
            </Typography>
            <List>
              <ListItem>
                <ListItemText primary="Required validation"></ListItemText>
              </ListItem>
              <ListItem>
                <ListItemText
                  primary="Asynchronous validation, which involves calling https://jsonplaceholder.typicode.com/photos
              and verifying the number or photos returned!"
                ></ListItemText>
              </ListItem>
              <ListItem>
                <ListItemText primary="Input length validation"></ListItemText>
              </ListItem>
            </List>
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            <TextField
              fullWidth
              variant="outlined"
              label="First Name"
              onChange={onChange(changeHandler)}
              helperText={
                error.length ? error.map(err => <div>{err}</div>) : ""
              }
              error={error.length ? true : false}
            ></TextField>
          </Paper>
        </Grid>
      </Grid>
      <Grid item xs={12} md={12}>
        <Paper className={classes.paper}>
          <Typography variant="h4" color="textPrimary">
            Documentation
          </Typography>
          <Typography color="textPrimary">
            WIP
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  );
};
